/* eslint-disable */
import React from 'react';
import styled from 'styled-components';

const StockInfoContainer = styled.div`
  background-color: #4CAF50; /* 초록색 배경 */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
`;

const Title = styled.h2`
  color: white;
  margin-top: 0;
  margin-bottom: 10px;
`;

const InfoItem = styled.p`
  color: white;
  margin: 5px 0;
`;

// 요일 계산 함수
const getWeekday = (dateString) => {
  const days = ['일요일', '월요일', '화요일', '수요일', '목요일', '금요일', '토요일'];
  const date = new Date(dateString);
  return days[date.getDay()];
};

// 날짜 포맷 함수 (YYYYMMDD -> YYYY-MM-DD)
const formatDate = (dateString) => {
  return `${dateString.substring(0, 4)}-${dateString.substring(4, 6)}-${dateString.substring(6, 8)}`;
};

function StockInfo({ data }) {
  if (!data) {
    return <p>데이터가 없습니다.</p>;
  }

  const formattedDate = formatDate(data.basDt); // 날짜 형식 변경
  const weekday = getWeekday(formattedDate); // 요일 계산
// 등락률 퍼센트 나타내기
  const fltRt = parseFloat(data.fltRt);
  const formattedFltRt = fltRt.toFixed(2) + '%';

    // 종가와 전일대비 값 처리
    const formattedClpr = data.clpr.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    const formattedVs = data.vs.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  

  return (
    <StockInfoContainer>
      <Title>주식 시세 정보:</Title>
      <InfoItem>회사명: {data.itmsNm}</InfoItem>
      <InfoItem>상장사: {data.mrktCtg}</InfoItem>
      <InfoItem>종가: {formattedClpr}원</InfoItem>
      <InfoItem>전일대비: {formattedVs}원</InfoItem>
      <InfoItem>등락률: {formattedFltRt}</InfoItem>
      <InfoItem>조회일자: {formattedDate} ({weekday})</InfoItem>
    </StockInfoContainer>
  );
}

export default StockInfo;
