/* eslint-disable */

import React, { useState } from 'react';
import SearchBar from './SearchBar';
import StockInfo from './StockInfo';
import axios from 'axios';
import convert from 'xml-js';
import styled from 'styled-components';

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
`;

const Title = styled.h1`
  background-color: #0af07a; /* 배경색 */
  color: #000000; /* 글자색 */
  border-radius: 50px; /* 동그란 버튼 모양 */
  padding: 10px 20px; /* 버튼 내부 여백 */
  font-size: 24px; /* 글자 크기 */
  font-weight: bold; /* 굵은 글씨 */
  margin-bottom: 20px; /* 제목과 검색창 사이 간격 */
`;

function App() {
  const [stockData, setStockData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchStockData = async (itmsNm, date) => {
    setLoading(true);
    setError(null);

    const API_KEY = 'CADGVCpJ6S3ugec34rtjEC4Fq1h0t0sbaD+chVRlpPGrKdOCDgyGmI0WnIpPQf4d7a4EPfo8FXmTmJqWxPrqrQ==';
    const API_URL = `https://apis.data.go.kr/1160100/service/GetStockSecuritiesInfoService/getStockPriceInfo`;

    try {
      const response = await axios.get(API_URL, {
        params: { 
          serviceKey: API_KEY,
          itmsNm: itmsNm,
          basDt: date ? date.replace(/-/g, '') : undefined // 날짜가 있을 경우 YYYYMMDD 형식으로 변환
        }
      });

      console.log('XML 응답:', response.data);

      const jsonResult = convert.xml2js(response.data, { compact: true, spaces: 2 });
      console.log('변환된 JSON:', jsonResult);

      const items = jsonResult.response.body.items.item;

      // 검색된 항목의 이름을 대소문자 구분 없이 비교
      const itemArray = Array.isArray(items) ? items : [items];
      const stockItem = itemArray.find(item => item.itmsNm._text.toLowerCase() === itmsNm.toLowerCase());

      if (stockItem) {
        const extractedData = {
          itmsNm: stockItem.itmsNm._text,
          mrktCtg: stockItem.mrktCtg._text,
          clpr: stockItem.clpr._text,
          vs: stockItem.vs._text,
          fltRt: stockItem.fltRt._text,
          basDt: stockItem.basDt._text
        };
        setStockData(extractedData);
      } else {
        setStockData(null);
        setError('해당 회사의 데이터를 찾을 수 없습니다.');
      }
    } catch (error) {
      console.error('API 호출 오류:', error);
      setError('회사명을 정확히 입력해주세요.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <AppContainer>
      <div className="App">
        <Title>주식 시세 정보 조회</Title>
        <SearchBar onSearch={fetchStockData} />
        {loading && <p>로딩 중...</p>}
        {error && <p style={{ color: 'red' }}>{error}</p>}
        {stockData && <StockInfo data={stockData} />}
      </div>
    </AppContainer>
  );
}

export default App;
